<template>
    <div>
    <b-container fluid class="content">
        <b-card border-variant="light" header="IOS Provider details" class="text-center">
            <alert-success ref="alertSuccess"> {{ successMessage }} </alert-success>
            <alert-fail ref="alertFail"> {{ failMessage }} </alert-fail>
            <b-card-text>
                <b-container fluid>
                    <!-- team id -->
                    <b-row class="my-2">
                        <b-col sm="3" class="text-right">
                        <label for="input-none" class="form-label">Team id : </label>
                        </b-col>
                        <b-col sm="9">
                        <b-form-input
                          :state="fieldError.teamId == null ? null: fieldError.teamId.isCorrect"
                          ref="teamId"
                          size="sm"
                          v-model="teamId"
                          placeholder="Required">
                        </b-form-input>
                          <b-form-invalid-feedback class="text-left" :state="false">
                            {{ fieldError.teamId == null ? null : fieldError.teamId.message }}
                          </b-form-invalid-feedback>
                        </b-col>
                    </b-row>
                    <!-- key id -->
                    <b-row class="my-2">
                        <b-col sm="3" class="text-right">
                        <label for="input-none" class="form-label">Key id : </label>
                        </b-col>
                        <b-col sm="9">
                        <b-form-input
                          :state="fieldError.keyId == null ? null: fieldError.keyId.isCorrect"
                          size="sm"
                          v-model="keyId"
                          placeholder="Required">
                        </b-form-input>
                        <b-form-invalid-feedback class="text-left" :state="false">
                            {{ fieldError.keyId == null ? null : fieldError.keyId.message }}
                          </b-form-invalid-feedback>
                        </b-col>
                    </b-row>
                    <!-- bundle id -->
                     <b-row class="my-2">
                        <b-col sm="3" class="text-right">
                        <label for="input-none" class="form-label">Bundle id : </label>
                        </b-col>
                        <b-col sm="9">
                        <b-form-input
                          :state="fieldError.bundleId == null ? null: fieldError.bundleId.isCorrect"
                          size="sm"
                          v-model="bundleId"
                          placeholder="Required">
                        </b-form-input>
                        <b-form-invalid-feedback class="text-left" :state="false">
                            {{ fieldError.bundleId == null ? null : fieldError.bundleId.message }}
                          </b-form-invalid-feedback>
                        </b-col>
                    </b-row>
                    <!-- p8 file -->
                    <b-row class="my-2">
                        <b-col sm="3" class="text-right">
                        <label for="input-none" class="form-label">p8 File : </label>
                        </b-col>
                        <b-col sm="9">
                        <b-form-file
                            :state="fieldError.file == null ? null: fieldError.file.isCorrect"
                            :placeholder="fileName"
                            v-model="file"
                            accept="p8">
                        </b-form-file>
                        <b-form-invalid-feedback class="text-left" :state="false">
                            {{ fieldError.file == null ? null : fieldError.file.message }}
                        </b-form-invalid-feedback>
                        </b-col>
                    </b-row>
                </b-container>
                <hr/>
                <b-row>
                    <b-col cols="5"></b-col>
                    <b-col class="border-right-gray">
                        <b-button size="sm" variant="outline-secondary" @click="$router.go(-1)"> back </b-button>
                    </b-col>
                    <b-col>
                        <b-button :disabled="!isActive" size="sm" variant="outline-primary" @click="submit"> {{ submitButtonName }} </b-button>
                    </b-col>
                    <b-col cols="5"></b-col>
                </b-row>
            </b-card-text>
        </b-card>
    </b-container>
    </div>
</template>

<script>
import AlertSuccess from '@/components/AlertSuccess';
import AlertFail from '@/components/AlertFail';

export default {
  props: {
    isActive: Boolean,
    appId: String
  },
  data () {
    return {
      successMessage: 'Update ios provider success. ',
      failMessage: '',
      fieldError: {},
      submitButtonName: 'Update',
      teamId: '',
      keyId: '',
      bundleId: '',
      file: null,
      fileName: 'choose your key.p8 file.'
    };
  },
  methods: {
    submit () {
      this.setDefault();
      const fileds = {
        teamId: this.teamId,
        keyId: this.keyId,
        bundleId: this.bundleId,
        file: this.file
      };
      this.$emit('submit', fileds);
    },
    getProvider () {
      const url = process.env.VUE_APP_SERVICE_HOST + '/app/ios/' + this.appId;
      this.axios.get(url)
        .then(response => {
          const data = response.data;
          if (!this._.isEmpty(data)) {
            this.teamId = data.teamId;
            this.keyId = data.keyId;
            this.bundleId = data.bundleId;
            // eslint-disable-next-line
            this.fileName = data.file.replace(/^.*[\\\/]/, '');
          }
        })
        .catch(error => {
          if (error.response) {
            this.showFail(`fail to get provider ${error.response.data} `);
          } else {
            this.showFail(`fail to get provider ${error.message} `);
          }
        });
    },
    showFail (message) {
      this.failMessage = message;
      this.$refs.alertFail.show();
    },
    showSuccess (message) {
      this.successMessage = message;
      this.$refs.alertSuccess.countDownChanged(3);
    },
    setFieldError (fields) {
      if (fields) {
        for (const field of fields) {
          this.fieldError[field.param] = { message: field.msg, isCorrect: false };
        }
      }
    },
    setDefault () {
      this.failMessage = '';
      this.fieldError = {};
      this.$refs.alertFail.hide();
    }
  },
  components: {
    AlertSuccess,
    AlertFail
  },
  mounted () {
    if (this.appId) {
      this.getProvider();
    }
  }
};
</script>

<style scoped>
    .content{
        margin-top: 20px;
        font-size: 0.8em;
    }
    .border-right-gray{
        border-right: 1px dashed rgba(207, 207, 207);
    }
    .form-label{
        color: #666666;
        line-height: 30px;
    }
</style>
