<template>
    <div>
        <ios-provider-form
        @submit="submit"
        :isActive="isActive"
        :appId="appId"
        ref="form"
        >
        </ios-provider-form>
    </div>
</template>

<script>
import IosProviderForm from '@/components/form/IosProviderForm';
export default {
  data () {
    return {
      isActive: true,
      appId: this.$route.params.appId
    };
  },
  components: {
    IosProviderForm
  },
  methods: {
    submit (value) {
      const url = process.env.VUE_APP_SERVICE_HOST + '/app/ios/create/' + this.appId;
      let bodyFormData = new FormData();
      bodyFormData.set('teamId', value.teamId);
      bodyFormData.set('keyId', value.keyId);
      bodyFormData.set('bundleId', value.bundleId);
      bodyFormData.set('p8', value.file);
      const options = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      };
      this.isActive = false;

      this.axios.put(url, bodyFormData, options)
        .then(response => {
          this.$refs.form.showSuccess('Update ios provider success. ');
        })
        .catch(error => {
          this.isActive = true;
          if (error.response) {
            const data = error.response.data;
            this.failMessage = data.topic;
            this.$refs.form.setFieldError(data.fields);
          } else {
            this.failMessage = error.message;
          }
          this.$refs.form.showFail(this.failMessage);
        });
    }
  }
};
</script>

